










import Vue from 'vue'
// import login from './view/login/login.vue'
import Component from 'vue-class-component'
@Component({})
export default class App extends Vue {
  userInfo: any = {
      name:'',
      password:''
    }
  // 保存登录数据到localStorage
  setStorage(): void{
      localStorage.setItem('userInfo', JSON.stringify(this.userInfo));
  }
}



